'use-client'

import { actions } from "@/actions";

import Link from "next/link";

import { Label } from "@/components/ui/label";
import { Input } from "@/components/ui/input";
import { Button } from "@/components/ui/button";

import {
    CardTitle,
    Card, CardContent,
} from "@/components/ui/card";

export function SignUpForm() {
    return (
        <div className="w-lg mx-auto p-6 bg-white shadow-md">
            <form>
                <Card className="flex-col justify-center items-center w-full h-auto">
                    <CardTitle className="flex w-auto h-[50px] justify-center content-center font-bold font-sans text-2xl">Sign Up</CardTitle>
                    <CardContent className="w-full">
                        <div className="space-y-4 w-full">
                            <div className="flex-row items-stretch align-middle content-around h-[70px] p-1">
                                <Label className="h-[20px] p-2" htmlFor="name">Name</Label>
                                <Input id="name" placeholder="Name" />
                            </div>
                            <div className="flex-row items-stretch align-middle content-around h-[70px] p-1">
                                <Label className="h-[20px] p-2" htmlFor="email">Email</Label>
                                <Input id="email" type="email" placeholder="Email" />
                            </div>
                            <div className="flex-row items-stretch align-middle content-around h-[70px] p-1">
                                <Label className="h-[20px] p-2" htmlFor="password">Password</Label>
                                <Input id="password" type="password" placeholder="Password" />
                            </div>
                        </div>
                    </CardContent>
                    <Button className="w-[40%] m-4 bg-amber-200 hover:bg-amber-400 text-blue-500">Sign Up</Button>
                </Card>
            </form>
        </div>
    )
}